var Gameid = "";


document.querySelector(".action-button").addEventListener("mousedown", function(){
    var PlayerAmount = document.querySelector(".c-actions__label").value;
    console.log(PlayerAmount);
    document.querySelector(".l-wrapper").style.display = "none";
    Gameid = generateGameId();
    firebaseRef = firebase.database().ref(Gameid);
    firebaseRef.update({
        "FreiePl√§tze": --PlayerAmount,
    });
    firebaseRef.update({
        "Players": username,
    });
    firebaseRef.update({
        "Admin": username,
    });
    document.querySelector(".GameSelect").style.display = "grid";
    console.log(Gameid);
    defaultgamescreen();
});

function generateGameId() {
    var possibleChars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var gameId = "";
    for (var j = 0; j < 8; j++) gameId += possibleChars.charAt(Math.floor(Math.random() * possibleChars.length));
    return gameId;
}



