function timerCountdown(timeLimit, timePassed) {
  const TIME_LIMIT = timeLimit;
  const WARNING_THRESHOLD = 15;
  const ALERT_THRESHOLD = 5;
  let TIME_PASSED = timePassed;
  const FULL_DASH_ARRAY = 283;
  let timeLeft = TIME_LIMIT;
  let timerInterval = null;
  const COLOR_CODES = {
    info: { color: "green" },
    warning: { color: "orange", threshold: WARNING_THRESHOLD },
    alert: { color: "red", threshold: ALERT_THRESHOLD }
  };
  let remainingPathColor = COLOR_CODES.info.color;

  document.getElementById("timer").innerHTML = `
  <div class="base-timer">
    <svg class="base-timer__svg" viewBox="0 0 100 100">
      <g class="base-timer__circle">
        <circle class="base-timer__path-elapsed" cx="50" cy="50" r="45"></circle>
        <path id="base-timer-path-remaining" stroke-dasharray="283" class="base-timer__path-remaining ${remainingPathColor}" d="M 50, 50 m -45, 0 a 45,45 0 1,0 90,0 a 45,45 0 1,0 -90,0"></path>
      </g>
    </svg>
    <span id="base-timer-label" class="base-timer__label">${formatTime(timeLeft)}</span>
  </div>
  `;

  startTimer();

  function onTimesUp() {
    rounds ++;
    enterpressed = false;
    clearInterval(timerInterval);
    if(rounds == 5){
        rounds = 0;
        document.querySelector(".game-full-flex ").style.display = "flex";
        enterpressed = false;
        document.querySelector(".base-timer").style.display = "none";
        document.getElementById("m").style.display = "none";
        document.querySelector(".printer").style.display = "none";
        document.querySelector(".shadows").style.display = "none";
        clearInterval(timerInterval);
        if(userscore > Enemyscore){
            document.querySelector(".final-score").innerHTML = username;
        }
        else if(userscore < Enemyscore){
            document.querySelector(".final-score").innerHTML = Enemy;
        }
        else{
            document.querySelector(".final-score").innerHTML = "Draw";
            document.querySelector(".result").innerHTML = "";
        }
    }
    else if(Admin == username){
        Random = Math.floor(Math.random() * image_array.length);
        while(Random == random_index){
            Random = Math.floor(Math.random() * image_array.length);
            random_index = Random;
        }
        selected_image = image_array[Random];

        selected_solution = solution_array[Random];
  
        firebaseRef = firebase.database().ref(GameKey);

        firebaseRef.child("Flag").set(selected_image);
        firebaseRef.child("Solution").set(selected_solution);
        
    }
    // document.getElementById("base-timer-label").innerHTML = "Done";
  }

  function startTimer() {
    timerInterval = setInterval(() => {
      TIME_PASSED = TIME_PASSED += 1;
      timeLeft = TIME_LIMIT - TIME_PASSED;
      // console.log(TIME_PASSED);
      document.getElementById("base-timer-label").innerHTML = formatTime(timeLeft);
      setCircleDasharray();
      setRemainingPathColor(timeLeft);
      if (timeLeft === 0) {
        onTimesUp();
      }
    }, 1000);
  }

  function formatTime(time) {
    let minutes = Math.floor(time / 60);
    // if (minutes < 10) {
    //   minutes = `0${minutes}`;
    // }
    let seconds = time % 60;
    if (seconds < 10) {
      seconds = `0${seconds}`;
    }
    return `${minutes}:${seconds}`;
  }

  function setRemainingPathColor(timeLeft) {
    const { alert, warning, info } = COLOR_CODES;
    if (timeLeft <= alert.threshold) {
       document.getElementById("base-timer-path-remaining").classList.remove(warning.color);
       document.getElementById("base-timer-path-remaining").classList.add(alert.color);
       document.getElementsByClassName("base-timer__label")[0].style.color = `${alert.color}`;
    } else if (timeLeft <= warning.threshold) {
       document.getElementById("base-timer-path-remaining").classList.remove(info.color);
       document.getElementById("base-timer-path-remaining").classList.add(warning.color);
       document.getElementsByClassName("base-timer__label")[0].style.color = `${warning.color}`;
    }
  }

  function calculateTimeFraction() {
    const rawTimeFraction = timeLeft / TIME_LIMIT;
    return rawTimeFraction - (1 / TIME_LIMIT) * (1 - rawTimeFraction);
  }

  function setCircleDasharray() {
    const circleDasharray = `${(calculateTimeFraction() * FULL_DASH_ARRAY).toFixed(0)} 283`;
    document.getElementById("base-timer-path-remaining").setAttribute("stroke-dasharray", circleDasharray);
  }
}














